<template>
  <div class="Words">
    <div class="canvas-container">
      <canvas id="c"></canvas>
    </div>
    <div class="home-container">
      <div class="container-index">
        <Row :gutter="12">
      <Col span="18" :xs="24" :sm="24" :md="16" :lg="18">
        <Card class="the-content">
          <h2 class="the-content-title" slot="title">闲言碎语的标题
          <p class="the-content-meta" slot="extra">
              <span class="postlist-meta-time" style="float: right">
                <Icon type="ios-clock-outline"></Icon>&nbsp;2017-03-29</span>
              <span class="postlist-meta-views"><Icon type="eye"></Icon>&nbsp;浏览: 56 </span>
              <span>&nbsp;&nbsp;&nbsp;&nbsp;<Icon type="android-favorite-outline"></Icon>&nbsp;100</span>
          </p>
          </h2>
          <div class="the-text">
            <p style="color:#555555;font-family:&quot;font-size:15px;background-color:#FFFFFF;">
              现如今这四海八荒都在用vue啊！年前我手头新做一个运营平台，截图如下，前后端分离，费了半天劲终于把环境搭建起来了，年后有功夫了，特地整理下，希望大家能少走些弯路！
            </p>
            <p style="color:#555555;font-family:&quot;font-size:15px;background-color:#FFFFFF;">
              <img src="http://img.blog.csdn.net/20170217144845072?watermark/2/text/aHR0cDovL2Jsb2cuY3Nkbi5uZXQvbGVtb25femhhbw==/font/5a6L5L2T/fontsize/400/fill/I0JBQkFCMA==/dissolve/70/gravity/Center" alt="" style="height:auto;" />
            </p>
            <p style="color:#555555;font-family:&quot;font-size:15px;background-color:#FFFFFF;">
              一、搭建环境
            </p>
            <p style="color:#555555;font-family:&quot;font-size:15px;background-color:#FFFFFF;">
              用官方的脚手架cli一步一步按流程走，我整理出的流程如下，参考官网指导&nbsp;<a target="_blank" href="https://cn.vuejs.org/v2/guide/installation.html#NPM">https://cn.vuejs.org/v2/guide/installation.html#NPM</a>
            </p>
            <p style="color:#555555;font-family:&quot;font-size:15px;background-color:#FFFFFF;">
              1. npm install vue-cli webpack -g （全局安装vue脚手架和webpack）<br />
              2. 进入工程目录 vue init webpack my-project （初始化，输入有的没的）<br />
              3. cd my-project<br />
              4. npm install （安装项目依赖，一定要用npm，会比较慢）<br />
              5. npm install vue-router vue-resource --save-dev（安装 vue 路由模块vue-router和网络请求模块vue-resource）<br />
              6. npm run dev（本地运行）<br />
              7. npm run build（生成服务器build）
            </p>
            <p style="color:#555555;font-family:&quot;font-size:15px;background-color:#FFFFFF;">
              二、路由配置
            </p>
            <p style="color:#555555;font-family:&quot;font-size:15px;background-color:#FFFFFF;">
              前面搭建环境的时候已经安装了router了，在配置里直接调用就可以了，参考官方指导和demo&nbsp;<a target="_blank" href="https://router.vuejs.org/zh-cn/essentials/getting-started.html">https://router.vuejs.org/zh-cn/essentials/getting-started.html</a>
            </p>
            <p style="color:#555555;font-family:&quot;font-size:15px;background-color:#FFFFFF;">
              由于代码里设计太多公司接口的东东，我就只展示部分路由代码了，framework.vue放的是路由的dom层，main.js是路由配置层
            </p>
            <p>
              <br />
            </p>
          </div>
          <!-- UY BEGIN -->
          <div id="uyan_frame"></div>
          <!-- UY END -->
        </Card>
      </Col>
      <Col span="6" :xs="0" :sm="0" :md="8" :lg="6">
        <Card class="card-list-new" style="padding-bottom: 6px;">
          <Tabs active-key="key1">
            <Tab-pane label="不如搜搜" icon="ios-search-strong">
              <Input v-model="value" placeholder="请输入关键字..." type="text"></Input>
            </Tab-pane>
          </Tabs>
        </Card>
        <Card>
        <Tabs active-key="key1" :animated="false">
          <Tab-pane label="Look around" icon="ios-paper">
            <article class="card-list-column card-list-column-around">
              <ul>
                <li class="card-list-column-li-tit clearfix">
                  <h3 class="title">
                    <Icon type="ios-plus-empty"></Icon>
                    <a href="javascript:;">Vue 组件提供了纯自定义元素所不具备的一些重要功能，最突出的是跨组件数据流，自定义事件通信以及构建工具集成。</a>
                  </h3>
                </li>
                <li class="card-list-column-li-tit clearfix">
                  <h3 class="title">
                    <Icon type="ios-plus-empty"></Icon>
                    <a href="javascript:;">在一个大型应用中，有必要将整个应用程序划分为组件，以使开发可管理。在后续教程中我们将详述组件，不过这里有一个（假想的）使用了组件的应用模板是什么样的例子</a>
                  </h3>
                </li>
                <li class="card-list-column-li-tit clearfix">
                  <h3 class="title">
                    <Icon type="ios-plus-empty"></Icon>
                    <a href="javascript:;">组件系统是 Vue 的另一个重要概念</a>
                  </h3>
                </li>
                <li class="card-list-column-li-tit clearfix">
                  <h3 class="title">
                    <Icon type="ios-plus-empty"></Icon>
                    <a href="javascript:;">数据绑定一个常见需求是操作元素的 class 列表和它的内联样式。因为它们都是属性 ，我们可以用v-bind 处理它们：只需要计算出表达式最终的字符串</a>
                  </h3>
                </li>
              </ul>
            </article>
          </Tab-pane>
        </Tabs>
      </Card>
      </Col>
    </Row>
      </div>
    </div>
  </div>
</template>
<script>
  import 'assets/style/master.css'
  export default {
    name: 'Words',
    data () {
      return {
        value: ''
      }
    },
    mounted() {
      var nowDate = new Date().getTime();
      const s = document.createElement('script');
      s.type = 'text/javascript';
      s.id = 'youYan';
      s.src = 'http://v2.uyan.cc/code/uyan.js?uid=2129405?time=' + nowDate;
      document.getElementById('app').appendChild(s);
    },
    created: function () {
    },
    mounted: function(){
      var canvas = document.getElementById('c'),
        ctx = canvas.getContext('2d'),
        w = canvas.width = window.innerWidth,
        h = canvas.height = 300,

        hue = 217,
        stars = [],
        count = 0,
        maxStars = 1400;

// Thanks @jackrugile for the performance tip! http://codepen.io/jackrugile/pen/BjBGoM
// Cache gradient
      var canvas2 = document.createElement('canvas'),
        ctx2 = canvas2.getContext('2d');
      canvas2.width = 100;
      canvas2.height = 100;
      var half = canvas2.width/2,
        gradient2 = ctx2.createRadialGradient(half, half, 0, half, half, half);
      gradient2.addColorStop(0.025, '#fff');
      gradient2.addColorStop(0.1, 'hsl(' + hue + ', 61%, 33%)');
      gradient2.addColorStop(0.25, 'hsl(' + hue + ', 64%, 6%)');
      gradient2.addColorStop(1, 'transparent');

      ctx2.fillStyle = gradient2;
      ctx2.beginPath();
      ctx2.arc(half, half, half, 0, Math.PI * 2);
      ctx2.fill();

// End cache

      function random(min, max) {
        if (arguments.length < 2) {
          max = min;
          min = 0;
        }

        if (min > max) {
          var hold = max;
          max = min;
          min = hold;
        }

        return Math.floor(Math.random() * (max - min + 1)) + min;
      }

      var Star = function() {

        this.orbitRadius = random(w / 2 - 50);
        this.radius = random(100, this.orbitRadius) / 10;
        this.orbitX = w / 2;
        this.orbitY = h / 2;
        this.timePassed = random(0, maxStars);
        this.speed = random(this.orbitRadius) / 100000;
        this.alpha = random(2, 10) / 10;

        count++;
        stars[count] = this;
      }

      Star.prototype.draw = function() {
        var x = Math.sin(this.timePassed + 1) * this.orbitRadius + this.orbitX,
          y = Math.cos(this.timePassed) * this.orbitRadius/2 + this.orbitY,
          twinkle = random(10);

        if (twinkle === 1 && this.alpha > 0) {
          this.alpha -= 0.05;
        } else if (twinkle === 2 && this.alpha < 1) {
          this.alpha += 0.05;
        }

        ctx.globalAlpha = this.alpha;
        ctx.drawImage(canvas2, x - this.radius / 2, y - this.radius / 2, this.radius, this.radius);
        this.timePassed += this.speed;
      }

      for (var i = 0; i < maxStars; i++) {
        new Star();
      }

      function animation() {
        ctx.globalCompositeOperation = 'source-over';
        ctx.globalAlpha = 0.8;
        ctx.fillStyle = 'hsla(' + hue + ', 64%, 6%, 1)';
        ctx.fillRect(0, 0, w, h)

        ctx.globalCompositeOperation = 'lighter';
        for (var i = 1, l = stars.length; i < l; i++) {
          stars[i].draw();
        };

        window.requestAnimationFrame(animation);
      }

      animation();
    },
    methods: {}
  }
</script>



