<template>
  <div class="Front">
    <div class="canvas-container">
      <canvas id="c"></canvas>
    </div>
    <div class="home-container">
      <div class="container-index">
      <Row :gutter="12">
        <Col span="18" :xs="24" :sm="24" :md="16" :lg="18">
          <Card class="card-list-new card-list-new-list">

          </Card>
        </Col>
        <Col span="6" :xs="0" :sm="0" :md="8" :lg="6">
          <Card class="card-list-new" style="padding-bottom: 6px;">
            <Tabs active-key="key1">
              <Tab-pane label="不如搜搜" icon="ios-search-strong">
                <Input v-model="value" placeholder="请输入关键字..." type="text"></Input>
              </Tab-pane>
            </Tabs>
          </Card>
          <Card>
            <Tabs active-key="key1" :animated="false">
              <Tab-pane label="Look around" icon="ios-paper">
                <article class="card-list-column card-list-column-around">
                  <ul>
                    <li class="card-list-column-li-tit clearfix">
                      <h3 class="title">
                        <Icon type="ios-plus-empty"></Icon>
                        <a href="javascript:;">Vue 组件提供了纯自定义元素所不具备的一些重要功能，最突出的是跨组件数据流，自定义事件通信以及构建工具集成。</a>
                      </h3>
                    </li>
                    <li class="card-list-column-li-tit clearfix">
                      <h3 class="title">
                        <Icon type="ios-plus-empty"></Icon>
                        <a href="javascript:;">在一个大型应用中，有必要将整个应用程序划分为组件，以使开发可管理。在后续教程中我们将详述组件，不过这里有一个（假想的）使用了组件的应用模板是什么样的例子</a>
                      </h3>
                    </li>
                    <li class="card-list-column-li-tit clearfix">
                      <h3 class="title">
                        <Icon type="ios-plus-empty"></Icon>
                        <a href="javascript:;">组件系统是 Vue 的另一个重要概念</a>
                      </h3>
                    </li>
                    <li class="card-list-column-li-tit clearfix">
                      <h3 class="title">
                        <Icon type="ios-plus-empty"></Icon>
                        <a href="javascript:;">数据绑定一个常见需求是操作元素的 class 列表和它的内联样式。因为它们都是属性 ，我们可以用v-bind 处理它们：只需要计算出表达式最终的字符串</a>
                      </h3>
                    </li>
                  </ul>
                </article>
              </Tab-pane>
            </Tabs>
          </Card>
        </Col>
      </Row>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Front',
  data () {
    return {
      value: ''
    }
  },
  created: function(){
  },
  mounted: function () {
    var OPT = {
      selector: "#c",
      amount: 10000,
      speed: 0.1, // pixels per frame
      lifetime: 500,
      direction: { x: -0.5, y: 1 },
      size: [1, 1],
      maxopacity: 1,
      color: "150, 150, 150",
      randColor: true,
      acceleration: [5, 40]
    };

    if (window.innerWidth < 520) {
      OPT.speed = 0.05;
      OPT.color = "150, 150, 150";
    }

    (function spark() {
      var canvas = document.querySelector(OPT.selector);
      var ctx = canvas.getContext("2d");

      var sparks = [];

      /*window.addEventListener('resize', function () {
        setCanvasWidth();
      });*/

      function setCanvasWidth() {
        ctx.canvas.width = window.innerWidth;
        ctx.canvas.height = 300;
      }

      function rand(min, max) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
      }

      // Init animation
      function init() {
        setCanvasWidth();

        window.setInterval(function () {
          if (sparks.length < OPT.amount) {
            addSpark();
          }
        }, 1000 / OPT.amount);

        window.requestAnimationFrame(draw);
      }

      function draw() {
        ctx.fillStyle = 'rgba(0,0,0, 0.08)';
        ctx.fillRect(0, 0, ctx.canvas.width, ctx.canvas.height);

        sparks.forEach(function (spark, i, array) {

          if (spark.opacity <= 0) {
            array.splice(i, 1);
          } else {
            drawSpark(spark);
          }
        });

        window.requestAnimationFrame(draw);
      }

      function Spark(x, y) {
        this.x = x;
        this.y = y;
        this.age = 0;
        this.acceleration = rand(OPT.acceleration[0], OPT.acceleration[1]);

        this.color = OPT.randColor ? rand(0, 255) + "," + rand(0, 255) + "," + rand(0, 255) : OPT.color;

        this.opacity = OPT.maxopacity - this.age / (OPT.lifetime * rand(1, 10));

        this.go = function () {
          this.x += OPT.speed * OPT.direction.x * this.acceleration / 2;
          this.y += OPT.speed * OPT.direction.y * this.acceleration / 2;

          this.opacity = OPT.maxopacity - ++this.age / OPT.lifetime;
        };
      }

      function addSpark() {
        var x = rand(-200, window.innerWidth + 200);
        var y = rand(-200, 500);
        sparks.push(new Spark(x, y));
      }

      function drawSpark(spark) {
        var x = spark.x,
          y = spark.y;

        spark.go();

        ctx.beginPath();
        ctx.fillStyle = "rgba(" + spark.color + ", " + spark.opacity + ")";
        ctx.rect(x, y, OPT.size[0], OPT.size[1], 0, 0, Math.PI * 2);
        ctx.fill();
      }

      init();
    })();
  },
  methods: {
  }
}
</script>

<style>
  .card-list-new-list .ivu-card-body{ padding-top: 20px;}
</style>



