<template>
  <div class="Study">
    <Card>
      <Row :gutter="10">
        <Col span="8">
        <div class="card-list" v-for="pic in nice1">
            <img class="nice-pic" :src="pic.thumb" :alt="pic.title" :title="pic.title"/>
        </div>
        </Col>
        <Col span="8">
        <div class="card-list" v-for="pic in nice2">
            <img class="nice-pic" :src="pic.picUrl" :alt="pic.title" :title="pic.title"/>
        </div>
        </Col>
        <Col span="8">
        <div class="card-list" v-for="pic in nice3">
            <img class="nice-pic" :src="pic.picUrl" :alt="pic.title" :title="pic.title"/>
        </div>
        </Col>
      </Row>
    </Card>
  </div>
</template>

<script>
export default {
  name: 'Study',
  data () {
    return {
      nice1 : {},
      nice2 : {},
      nice3 : {}
    }
  },
  created: function(){
    const _this = this;
    var nowDate = new Date().getTime();
    this.$http.get('http://route.showapi.com/819-1?showapi_appid=34405&showapi_timestamp=' + nowDate + '&showapi_sign=7bf34ce5e7a14e1ebb1ba08104079f65&num=10&page=1')
      .then(function (data) {
        _this.nice1 = data.data.showapi_res_body;
      })
      .catch(function (data) {
      });
    this.$http.get('http://route.showapi.com/197-1?showapi_appid=34405&showapi_timestamp=' + nowDate + '&showapi_sign=7bf34ce5e7a14e1ebb1ba08104079f65&num=10&')
      .then(function (data) {
        _this.nice2 = data.data.showapi_res_body.newslist;
      })
      .catch(function (data) {
      });
    this.$http.get('http://route.showapi.com/197-1?showapi_appid=34405&showapi_timestamp=' + nowDate + '&showapi_sign=7bf34ce5e7a14e1ebb1ba08104079f65&num=10&')
      .then(function (data) {
        _this.nice3 = data.data.showapi_res_body.newslist;
      })
      .catch(function (data) {
      });
  },
  methods: {
  }
}
</script>
<style>
  .Study .ivu-card-body{ padding: 10px;}
  .Study .card-list{ display: block; margin-bottom: 10px;}
  .Study .card-list .nice-pic{ display: block; width: 100%;}
</style>



