                        <p class="card-list-one-day">想一千次，不如去做一次，华丽的跌倒，胜过无所谓的徘徊。</p>
                      </article>
                    </Card>
                    <Card class="card-list-new" style="padding-bottom: 6px;">
                      <Tabs active-key="key1">
                        <Tab-pane label="不如搜搜" icon="ios-search-strong">
                          <Input v-model="value" placeholder="请输入关键字..." type="text"></Input>
                        </Tab-pane>
                      </Tabs>
                    </Card>
                    <Card>
                      <article class="card-list">
                        <Tabs active-key="key1" :animated="false">
                          <Tab-pane label="热门标签" icon="navigate">
                            <ul class="card-list-classify clearfix">
                              <li><a href="javascript:;"><Icon type="social-angular"></Icon>AngularJs</a></li>
                              <li><a href="javascript:;"><Icon type="social-vimeo"></Icon>VueJs</a></li>
                              <li><a href="javascript:;"><Icon type="social-html5"></Icon>Html5</a></li>
                              <li><a href="javascript:;"><Icon type="social-css3"></Icon>Css3</a></li>
                              <li><a href="javascript:;"><Icon type="social-javascript"></Icon>JavaScript</a></li>
                              <li><a href="javascript:;"><Icon type="social-chrome"></Icon>Chrome</a></li>
                            </ul>
                          </Tab-pane>
                        </Tabs>
                      </article>
                    </Card>
                    <Card>
                      <article class="card-list card-list-time-line">
                        <Tabs active-key="key1" :animated="false">
                          <Tab-pane label="岁月鎏金" icon="ios-pulse-strong">
                            <Timeline>
                              <Timeline-item color="red">
                                <p class="time">2017.03.01</p>
                                <p class="content">开始着手网站制作</p>
                              </Timeline-item>
                              <Timeline-item color="green">
                                <p class="time">2017.03.07</p>
                                <p class="content">构思了自己的一个网站布局</p>
                              </Timeline-item>
                              <Timeline-item color="blue">
                                <p class="time">2017.03.08</p>
                                <p class="content">对网站的设计进行代码实施</p>
                              </Timeline-item>
                              <Timeline-item color="red">
                                <p class="time">2017.03.09</p>
                                <p class="content">使用了vue1X + vivew构建网站demo</p>
                              </Timeline-item>
                              <Timeline-item><a href="javascript:;" class="link">更多的...</a></Timeline-item>
                            </Timeline>
                          </Tab-pane>
                        </Tabs>
                      </article>
                    </Card>
                  </Col>
              </Row>
            </div>
        </div>
    </div>
</template>

<script>
    import 'assets/style/home.css'
    export default {
        name: 'Home',
        data () {
            return {
              value : '',
              news : {}
            }
        },
        mounted: function () {
            var w = c.width = document.body.clientWidth,
                h = c.height = 300,
                ctx = c.getContext('2d'),
                opts = {
                    len: 20,
                    count: 50,
                    baseTime: 10,
                    addedTime: 10,
                    dieChance: .05,
                    spawnChance: 1,
                    sparkChance: .1,
                    sparkDist: 10,
                    sparkSize: 2,
                    color: 'hsl(hue,100%,light%)',
                    baseLight: 50,
                    addedLight: 10,
                    shadowToTimePropMult: 6,
                    baseLightInputMultiplier: .01,
                    addedLightInputMultiplier: .02,
                    cx: w / 2,
                    cy: h / 2,
                    repaintAlpha: .04,
                    hueChange: .1
                },
                tick = 0,
                lines = [],
                dieX = w / 2 / opts.len,
                dieY = h / 2 / opts.len,
                baseRad = Math.PI * 2 / 6;
                ctx.fillStyle = 'black';
                ctx.fillRect(0, 0, w, h);
            function loop() {
                window.requestAnimationFrame(loop);
                ++tick;
                ctx.globalCompositeOperation = 'source-over';
                ctx.shadowBlur = 0;
                ctx.fillStyle = 'rgba(0,0,0,alp)'.replace('alp', opts.repaintAlpha);
                ctx.fillRect(0, 0, w, h);
                ctx.globalCompositeOperation = 'lighter';
                if (lines.length < opts.count && Math.random() < opts.spawnChance) lines.push(new Line);
                lines.map(function (line) {
                    line.step()
                })
            }

            function Line() {
                this.reset()
            }

            Line.prototype.reset = function () {
                this.x = 0;
                this.y = 0;
                this.addedX = 0;
                this.addedY = 0;
                this.rad = 0;
                this.lightInputMultiplier = opts.baseLightInputMultiplier + opts.addedLightInputMultiplier * Math.random();
                this.color = opts.color.replace('hue', tick * opts.hueChange);
                this.cumulativeTime = 0;
                this.beginPhase()
            };
            Line.prototype.beginPhase = function () {
                this.x += this.addedX;
                this.y += this.addedY;
                this.time = 0;
                this.targetTime = (opts.baseTime + opts.addedTime * Math.random()) | 0;
                this.rad += baseRad * (Math.random() < .5 ? 1 : -1);
                this.addedX = Math.cos(this.rad);
                this.addedY = Math.sin(this.rad);
                if (Math.random() < opts.dieChance || this.x > dieX || this.x < -dieX || this.y > dieY || this.y < -dieY) this.reset()
            };
            Line.prototype.step = function () {
                ++this.time;
                ++this.cumulativeTime;
                if (this.time >= this.targetTime) this.beginPhase();
                var prop = this.time / this.targetTime,
                        wave = Math.sin(prop * Math.PI / 2),
                        x = this.addedX * wave,
                        y = this.addedY * wave;
                ctx.shadowBlur = prop * opts.shadowToTimePropMult;
                ctx.fillStyle = ctx.shadowColor = this.color.replace('light', opts.baseLight + opts.addedLight * Math.sin(this.cumulativeTime * this.lightInputMultiplier));
                ctx.fillRect(opts.cx + (this.x + x) * opts.len, opts.cy + (this.y + y) * opts.len, 2, 2);
                if (Math.random() < opts.sparkChance) ctx.fillRect(opts.cx + (this.x + x) * opts.len + Math.random() * opts.sparkDist * (Math.random() < .5 ? 1 : -1) - opts.sparkSize / 2, opts.cy + (this.y + y) * opts.len + Math.random() * opts.sparkDist * (Math.random() < .5 ? 1 : -1) - opts.sparkSize / 2, opts.sparkSize, opts.sparkSize)
            };
            loop();
            window.addEventListener('resize',
                    function () {
                        w = c.width = document.body.clientWidth;
                        h = c.height = 300;
                        ctx.fillStyle = 'black';
                        ctx.fillRect(0, 0, w, h);
                        opts.cx = w / 2;
                        opts.cy = h / 2;
                        dieX = w / 2 / opts.len;
                        dieY = h / 2 / opts.len
                    });

        }
        ,
        created: function(){
          var _this = this;
          var nowDate = new Date().getTime();
          this.$http.get('')
            .then(function (data) {
              _this.news = data;
            })
            .catch(function (data) {
            });
        },
        methods: {}
    }
</script>



